<style lang="scss" scoped="scoped">
	$width: 2px;
	$bg: #444;
	$textColor: #ababab;
	.section-nav-line {
		position: fixed;
		bottom: 0;
		left: 0;
		right: 0;
		margin: auto;
		background-color: #444;
		height: 7.5%;
		width: $width;
		overflow:hidden;
		z-index: 5;
		&:before {
			content: '';
			position: absolute;
			top: 0;
			left: 0;
			width: $width;
			height: 40%;
			background: $textColor;
			animation-name: sectionNavLine;
			animation-timing-function: linear;
			animation-duration: 2s;
			animation-iteration-count: infinite;
		}
	}

	.section-title {
		position: fixed;
		bottom: 8.5%;
		font-size: 1em;
		color: $textColor;
		width: 100%;
		text-align: center;
	}

	@keyframes sectionNavLine {
		0% {
			top: -40%;
		}
		60%,100% {
			top: 100%;
		}
	}
</style>

<template>
	<div style="z-index: 8;position: relative; cursor: pointer;">
		<span class="section-title">{{title}}</span>
		<div class="section-nav-line"></div>
	</div>
	
</template>

<script>
	export default {
		props: {
			title: {
				type: String,
				default: 'Next Section'
			}
		},
	}
</script>


